<template>
    <div class="search-panel">
        <div class="scroll-wrapper">
            <!--<div class="navigation-list">-->
            <!--<div class="navigation-list-item" v-for="item in searchList" @click="handlerNavigationItem(item)">-->
            <!--<div style="margin-bottom: 9px">-->
            <!--<mu-avatar size="45"-->
            <!--:style="`padding: 6px;`"-->
            <!--:color="item.typeId | formatFind(icons, 'color', 'typeId')">-->
            <!--<img style="padding: 6px" :src="item.typeId | formatFind(icons, 'value', 'typeId')">-->
            <!--</mu-avatar>-->
            <!--</div>-->
            <!--<span>{{item.value}}</span>-->
            <!--</div>-->
            <!--</div>-->
            <div class="history-wrapper">
                <div class="history-list" v-if="historyList.length">
                    <div class="history-list-item" v-for="(item, index) in historyList"
                         @click="handlerHistoryItem(item, index)">
                        <div class="history-list-item-content">
                            <mu-avatar class="history-list-item-content-icon" size="16" color="transparent">
                                <img :src="require('../../../static/icon/search_bar.png')">
                            </mu-avatar>
                            <span>{{item.query}}</span>
                        </div>
                        <mu-icon class="delete" value="close" @click.stop="handlerRemove(item)"/>
                    </div>
                    <div>
                        <mu-button class="history-clear" color="#989898"
                                   @click="handlerClear" flat>清空历史记录
                        </mu-button>
                    </div>
                </div>
                <no-success text="还没有留下历史数据" height="80vh" v-else/>
            </div>
        </div>
    </div>
</template>

<script>
    import {MapMixins} from "../../common/js/mixins"
    import {searchMixins, appMixins} from "../../common/mixins"
    import NoSuccess from "../../components/no-success/no-success"
    import Elements from '../../common/utils/elements'

    export default {
        name: "search-panel",
        mixins: [appMixins, searchMixins, MapMixins],
        data() {
            return {
                list: []
            }
        },
        computed: {
            historyList() {
                return this.history.map(item => {
                    return JSON.parse(item)
                })
            }
        },
        methods: {
            // 移除历史纪录
            handlerRemove(target) {
                target = JSON.stringify(target)

                this.deleteSearchHistory(target)
            },
            // 清空历史纪录
            handlerClear() {
                this.clearSearchHistory()
            },
            // 点击定位
            handlerLocation(item) {
                this.$toast({
                    message: '定位中 ...',
                    duration: 0
                })

                this.setCenter(item.x, item.y).then(res => {
                    this.$toast({
                        message: '定位成功',
                        duration: 300,
                    })

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast({
                        message: '定位失败',
                        duration: 500
                    })
                })
            },
            // 点击历史纪录
            handlerHistoryItem(item) {
                this.$emit('change', item.query)

                // if (this.$route.query.id && item.id) {
                //     this.$router.replace({
                //         name: 'search',
                //         query: {
                //             id: item.id
                //         }
                //     })
                //
                //     return
                // }

                // if (item.id) {
                //     this.$router.push({
                //         name: 'search',
                //         query: {
                //             id: item.id
                //         }
                //     })
                // }
            },
            handlerNavigationItem(item) {
                const id = this.$route.query.id

                this.$emit('change', '')

                if (id) {
                    this.$router.replace({
                        name: 'search',
                        query: {
                            id: item.typeId
                        }
                    })
                } else {
                    this.$router.push({
                        name: 'search',
                        query: {
                            id: item.typeId
                        }
                    })
                }
            }
        },
        filters: {
            formatElement(id) {
                const result = Elements.find(item => {
                    return item.id === id
                })

                return result ? result.name : ''
            }
        },
        components: {
            NoSuccess
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"
    @import "../../common/stylus/mixins.styl"

    .search-panel
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 1
        padding-top 70px
        background-color rgba(233, 233, 233, .9)
        overflow hidden
        .scroll-wrapper
            width 100%
            height 100%
        .navigation-list
            display flex
            flex-wrap wrap
            margin 0 13px
            padding 5px
            margin-bottom 10px
            background-color #fff
            border-radius 5px
            .navigation-list-item
                padding 10px 0
                width 20%
                display flex
                flex-direction column
                align-items center
                > span
                    font-size $font-size-ms
        .history-wrapper
            margin 0 13px 20px
            min-height calc(100% - 20px)
            background-color #fff
            border-radius 5px
            .history-list
                .history-list-item
                    position relative
                    display flex
                    align-items center
                    padding 0 15px
                    height 50px
                    &:after
                        bottom-line(#efefef)
                    .delete
                        color #ccc
                    .history-list-item-content
                        flex 1
                        display flex
                        align-items center
                        font-size 16px
                        .history-list-item-content-icon
                            margin-right .3em
                            img
                                display block
                .history-clear
                    width 100%
                    height 47px
</style>



