<template>
    <div class="views search">
        <div class="search-wrapper shadow" :class="{visible: animation}">
            <transition name="fade" mode="in-out">
                <div class="back" @click="back" v-if="!$route.query.id">
                    <mu-icon value="navigate_before" size="30"/>
                </div>
                <div class="badge" :style="`background-color: ${target.color}`" @click="back" v-else>
                    <img class="badge-image" :src="target.icon" :data-id="target.id">
                    <div class="badge-inner">
                        <span>{{target.name}}</span>
                    </div>
                    <mu-icon size="20" color="#fff" value="close"/>
                </div>
            </transition>
            <div class="input-wrapper">
                <field class="input" v-model="query" placeholder="搜索船名、航标、桥梁、水位站 ……" ref="field"/>
            </div>
        </div>

        <template v-show="!query">
            <transition name="views-down">
                <search-panel v-if="visible" @change="change"/>
            </transition>
        </template>

        <div class="result-wrapper" v-show="query">
            <result :query="query"/>
        </div>

        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Result from './result'
    import SearchPanel from './search-panel'
    import {appMixins, searchMixins} from "../../common/mixins"

    const SEARCH = 'SEARCH'

    export default {
        name: SEARCH,
        mixins: [appMixins, searchMixins],
        data() {
            return {
                query: '',
                animation: false
            }
        },
        computed: {
            visible() {
                return this.$route.name === 'search'
            },
            target() {
                const id = this.$route.query.id

                const icon = this.icons.find(item => {
                    return item.typeId === id
                })

                const item = this.searchList.find(item => {
                    return item.typeId === id
                })

                return {
                    color: icon ? icon.color : '',
                    icon: icon ? icon.value : '',
                    name: item ? item.value : '',
                    id
                }
            }
        },
        activated() {
            this.animation = true

            this.query = ''
        },
        deactivated() {
            this.animation = false
        },
        methods: {
            change(value) {
                this.query = value

                this.$refs.field.$refs.input.focus()
            },
            back() {
                // if (this.query) {
                //     this.query = ''
                // }

                this.$router.replace('/')
            }
        },
        components: {
            Result,
            SearchPanel
        }
    }
</script>

<style lang="stylus">
    .search
        padding-top 50px
        overflow hidden
        > .search-wrapper
            position absolute
            top 0
            left 0
            right 0
            z-index 999
            display flex
            align-items center
            margin 10px 13px
            padding 0 10px
            height 50px
            background-color #fff
            border-radius 5px
            overflow hidden
            transition all .5s
            .badge
                display flex
                align-items center
                justify-content center
                padding-right 5px
                height 25px
                border-radius 25px
                .badge-image
                    display block
                    padding 4px
                    width 25px
                    height 25px
                .badge-inner
                    padding 0 3px
                    height 25px
                    line-height 25px
                    color #fff
                    font-size 14px
            .input-wrapper
                flex 1
        > .result-wrapper
            position absolute
            top 0
            left 0
            right 0
            bottom 0
            z-index 100
</style>



