<template>
    <div class="result-list">
        <div class="scroll-wrapper">
            <div class="result-wrapper">
                <mu-load-more :loading="loading" v-if="list.length" @load="onLoad">
                    <mu-list style="padding: 0">
                        <mu-list-item class="border-1px-b"
                                      v-for="(item, index) in list" :key="item.id"
                                      @click.native="handlerItem(item, index)">
                            <mu-list-item-action style="min-width: 30px">
                                <mu-avatar size="22" color="transparent">
                                    <img :src="item.typeId | formatFind(searchIcons, 'value', 'typeId')"
                                         :data-id="item.typeId">
                                </mu-avatar>
                            </mu-list-item-action>
                            <mu-list-item-title class="no-wrap" style="padding-right: 1em">
                                {{item.name}}
                            </mu-list-item-title>
                            <mu-list-item-action style="min-width: 30px">
                                <mu-button color="#2196f3" @click.stop="collection(item)"
                                           v-loading="item.loading"
                                           :disabled="item.loading"
                                           small flat>
                                    设为常用
                                </mu-button>
                            </mu-list-item-action>
                        </mu-list-item>
                    </mu-list>
                </mu-load-more>
                <no-success text="暂无搜索结果" height="80vh" v-else/>
            </div>
        </div>
    </div>
</template>

<script>
    import {debounce} from "../../types/index"
    import {getSearchList} from "../../api/search"
    import {saveOrUpdateIAddress} from "../../api/personal"
    import NoSuccess from "../../components/no-success/no-success"
    import {searchMixins} from "../../common/mixins"
    import {isArray} from "../../types/index"

    export default {
        name: "result-list",
        mixins: [searchMixins],
        data() {
            return {
                loading: false,
                list: [],
                params: {
                    name: '',
                    // page: 1,
                    pageNumber: 1,
                    pageSize: 20
                }
            }
        },
        props: {
            query: {
                type: String,
                default: ''
            }
        },
        computed: {
            id() {
                return this.$route.query.id
            }
        },
        created() {
            this.$watch('query', debounce(value => {
                this.params.name = value

                if (!value) return

                this.getSearchList()
            }, 300))
        },
        activated() {
            this.list = []
        },
        methods: {
            // 点击收藏
            collection(item) {
                item.loading = true

                const data = {
                    name: item.name,
                    longitude: item.longitude,
                    latitude: item.latitude
                }

                saveOrUpdateIAddress(data).then(res => {
                    item.loading = false

                    this.$toast('添加常用地址成功')
                }).catch(err => {
                    item.loading = false

                    this.$toast('添加常用地址失败')
                })
            },
            // 请求搜索信息
            getSearchList() {
                this.params.pageNumber = 1
                // this.params.page = 1

                // if (this.id) {
                //     this.params.typeId = this.id
                //     getNavigationList(this.params).then(res => {
                //         this.list = isArray(res)
                //     })
                // } else {
                getSearchList(this.params).then(res => {
                    this.list = isArray(res)
                })
                // }
            },
            // 加载
            onLoad() {
                // this.params.page++
                this.params.pageNumber++
                this.loading = true

                // if (this.id) {
                //     this.params.typeId = this.id
                //     getNavigationList(this.params).then(res => {
                //         this.loading = false
                //         this.list.push(...isArray(res))
                //     })
                // } else {
                getSearchList(this.params).then(res => {
                    this.loading = false
                    this.list.push(...isArray(res))
                })
                // }
            },
            // 点击
            handlerItem(item) {
                const data = {
                    query: this.query
                }

                this.$router.push({
                    path: '/search',
                    query: data
                })

                this.saveSearchHistory(JSON.stringify(data))

                this.$router.push({
                    name: 'search-detail',
                    params: {
                        type: item.subType || item.typeId || this.$route.query.id,
                        id: item.id
                    },
                    query:{
                        typeId: item.typeId
                    }
                })
            }
        },
        watch: {
            id(value) {
                this.getSearchList()
            }
        },
        components: {
            NoSuccess
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .result-list
        padding-top 70px
        width 100%
        height 100%
        background-color rgba(239, 239, 239, .9)
        .scroll-wrapper
            width 100%
            height 100%
            .result-wrapper
                margin 0 13px 20px
                min-height calc(100% - 20px)
                background-color rgba(255, 255, 255, .9)
                border-radius 5px
                >>> .mu-item
                    padding-right 5px
        .text
            font-size $font-size-sm
            text-align right
            color #BEBEBE
            .d
                color #48AED9
</style>



